//FONTS

@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//START

@font-face {
	@include fontdefault(normal, 400, 'Open-Sans');
	@include fontpath('OpenSans-Regular', 'OpenSans-Regular', 'OpenSans-Regular'); }

@font-face {
	@include fontdefault(normal, 400, 'Roboto');
	@include fontpath('Montserrat-Regular', 'Montserrat-Regular', 'Montserrat-Regular'); }

@font-face {
	@include fontdefault(normal, 700, 'Montserrat');
	@include fontpath('Montserrat-Bold', 'Montserrat-Bold', 'Montserrat-Bold'); }

